/* src/components/Skills.css */

.skills-section {
    padding: 60px 20px;
    background-color: #1a1a1a;
    color: white;
    text-align: center;
  }
  
  .skills-list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 30px;
  }
  
  .skill-item {
    background-color: #333;
    padding: 15px 30px;
    border-radius: 8px;
    font-size: 1.2rem;
  }
/* 画面幅768px以下（スマホ向け） */
@media (max-width: 768px) {
    .skills-list {
      flex-direction: column;
      align-items: center;
    }
    .skill-item {
      width: 80%;
      padding: 12px 20px;
      font-size: 1rem;
    }
  }
    