/* src/components/ProfileCard.css */
.profile-section {
    background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
    padding: 60px 20px;
    color: #ffffff;
  }
  
  .profile-container {
    max-width: 600px;
    margin: 0 auto;
    border-radius: 20px;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.4);
    background-color: rgba(0, 0, 0, 0.3);
    padding: 40px;
    backdrop-filter: blur(8px);
  }
  
  .profile-title {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 30px;
    font-weight: bold;
    border-bottom: 2px solid #00ffc8;
    display: inline-block;
    padding-bottom: 8px;
  }
  
  .profile-card ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .profile-card li {
    font-size: 1.1rem;
    margin-bottom: 15px;
    border-bottom: 1px dashed #ccc;
    padding-bottom: 8px;
  }
  